const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			pageNode: [],
			imgUrl: '',
			categoryList:[],
			subCategoryList:[],
			page: '1',
			page_size: '10',
			boardId: '',
			categoryActive: 0,
			activeStyle: {
				color: this.activeTextColor,
				backgroundColor: this.activeBackgroundColor
			},
			vip:'',
			token: '',
			isShow: true,
		};
	},
	props: {
		//主分类激活索引
		defaultActive: {
			type: Number,
			default: 0
		},
		activeTextColor: {
			type: String,
			default: '#333'
		},
		activeBackgroundColor: {
			type: String,
			default: '#ffffff'
		},
	},
	onLaunch() {
		  
	},
	onShow() {
		
	},
	onHide() {
		
	},
	onShareAppMessage(){
		return {
			title: '子诺新微金分享',
			path: 'pages/index'
		}
	},
	onLoad() {
		this.token = uni.getStorageSync('token')
		this.imgUrl = helper.imgUrl
		this.getNav()
		this.categoryActive = 0
		this.subCategoryList = []
		this.getList()
	},
	methods: {
		// 是否获取过token
		getIsToken(){
			if (this.token == "") {
				// 获取缓存中用于登录的用户名和密码
				// 如果没有缓存信息,不进行登录,用户点击操作时,提示进入登录页
				const loginName = uni.getStorageSync('login_name');
				const loginPwd = uni.getStorageSync('login_pwd');
				// console.log(loginName + '---===---' + loginPwd)
				if (loginName == '' || loginPwd == '') {
					uni.showToast({
						title: '未检测到用户的登录记录，请进行登录',
						icon: 'none',
						duration: 3000
					});
					setTimeout(() => {
						// 进入登录页
						uni.reLaunch({
							url: './login'
						});
					}, 3000)
					
				} else {
					// 执行登录操作
					this.runLogin(loginName, loginPwd)
				}
			} else {
				// 获取用户信息
				this.getUserInfo()
				// this.getList()
			}
		},
		// 进行登录操作
		runLogin(loginName, loginPwd) {
			uni.request({
				url: `${helper.requestUrl}/login`,
				method: 'POST',
				data: {
					username: loginName,
					password: loginPwd
				},
				success: res => {
					console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
						// 登录的账号和密码存入缓存
						uni.setStorageSync('login_name', this.loginName);
						uni.setStorageSync('login_pwd', this.loginPaw);
						this.token = `${res.data.token_type} ${res.data.access_token}`
						uni.setStorageSync('token', this.token);
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 3000
						});
						setTimeout(() => {
							// 进入登录页
							uni.reLaunch({
								url: './login'
							});
						}, 3000)
					}
				}
			});
		},
		categoryMainClick(id,index,name){
			this.boardId = id
			this.page = '1'
			this.page_size = '10'
			this.subCategoryList = []
			this.categoryActive = index;
			this.getNavList()
		},
		categorySubClick(e){
			// console.log(e);
			if (this.token == "") {
				const loginName = uni.getStorageSync('login_name');
				const loginPwd = uni.getStorageSync('login_pwd');
				// console.log(loginName + '---===---' + loginPwd)
				if (loginName == '' || loginPwd == '') {
					uni.showToast({
						title: '未检测到用户的登录记录，请进行登录',
						icon: 'none',
						duration: 3000
					});
					setTimeout(() => {
						// 进入登录页
						uni.reLaunch({
							url: './login'
						});
					}, 2000)
					
				} else {
					// 执行登录操作
					this.runLogin(loginName, loginPwd)
					uni.navigateTo({
						url:`/pages/articleDetail?id=${e.id}`
					})
				}
			}else{
				
				uni.navigateTo({
					url:`/pages/articleDetail?id=${e.id}`
				})
			}
		},
		// 获取导航
		getNav(){
			uni.request({
				url: `${helper.requestUrl}/board/boards`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						this.categoryList = res.data.data
						this.boardId = res.data.data[0].id
						this.getNavList()
						// console.log(this.boardId)
					} else {
						if(res.data.message == '用户不存在或登陆已过期'){
							uni.showToast({
								title: '未检测到用户的登录记录，请进行登录',
								icon: 'none',
								duration: 3000
							});
							setTimeout(() => {
								// 进入登录页
								uni.reLaunch({
									url: './login'
								});
							}, 3000)
						}else{
							uni.showToast({
								title: res.data.message,
								icon: 'none',
								duration: 3000
							});
						}
					}
			
				}
			})
		},
		//获取数据
		getNavList(){
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data:{
					board_id: this.boardId,
					page_size: this.page_size,
					page: this.page
				},
				success: res => {
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						 this.subCategoryList = this.subCategoryList.concat(res.data.data)
						 if(res.data.data == 0){
						 	uni.showToast({
						 		title:'暂无更多数据',
						 		icon:"none"
						 	})
							this.isShow = true
							// console.log(this.isShow )
						 }else{
							 this.isShow =false
						 }
						
					} else if(res.data.status_code == 202) {
						this.vip = res.data.message
						this.isShow = false
					}else{
						uni.showToast({
							title: res.data.message,
							icon:'none',
							duration:2000
						});
					}
			
				}
			})
		},
		toLowFun() {
			this.page++;
			this.getNavList()
		},
		// 导航详情
		goNavs(e) {
			let link = e.currentTarget.dataset.link
			let bind_board = e.currentTarget.dataset.bind_board
			let id = e.currentTarget.dataset.id
			let name = e.currentTarget.dataset.name
			if (this.token == "") {
				const loginName = uni.getStorageSync('login_name');
				const loginPwd = uni.getStorageSync('login_pwd');
				// console.log(loginName + '---===---' + loginPwd)
				if (loginName == '' || loginPwd == '') {
					uni.showToast({
						title: '未检测到用户的登录记录，请进行登录',
						icon: 'none',
						duration: 3000
					});
					setTimeout(() => {
						// 进入登录页
						uni.reLaunch({
							url: './login'
						});
					}, 2000)
				} else {
					// 执行登录操作
					this.runLogin(loginName, loginPwd)
					if (bind_board == '0') {
						uni.navigateTo({
							url: `/pages/${link}`
						})
					} else{
						uni.navigateTo({
							url: `/pages/indexA?id=${bind_board}&name=${name}`
						})
					}
				}
			}else{
				if (bind_board == '0') {
					uni.navigateTo({
						url: `/pages/${link}`
					})
				} else{
					uni.navigateTo({
						url: `/pages/indexA?id=${bind_board}&name=${name}`
					})
				}
			}
			// console.log(e.currentTarget.dataset.id)
			
			// console.log(bind_board)
		},
		// 轮播跳转
		goBanner(e) {
			// console.log(e)
		},
		// 文章详情
		goDetail(e) {
			if (this.token == "") {
				const loginName = uni.getStorageSync('login_name');
				const loginPwd = uni.getStorageSync('login_pwd');
				// console.log(loginName + '---===---' + loginPwd)
				if (loginName == '' || loginPwd == '') {
					uni.showToast({
						title: '未检测到用户的登录记录，请进行登录',
						icon: 'none',
						duration: 3000
					});
					setTimeout(() => {
						// 进入登录页
						uni.reLaunch({
							url: './login'
						});
					}, 2000)
					
				} else {
					// 执行登录操作
					this.runLogin(loginName, loginPwd)
					uni.navigateTo({
						url: `/pages/articleDetail?id=${e.currentTarget.dataset.id}`
					})
				}
			}else{
				uni.navigateTo({
					url: `/pages/articleDetail?id=${e.currentTarget.dataset.id}`
				})
			}
			
		},
		getUserInfo() {
			// 用户信息获取
			uni.showLoading({
			  title: '用户信息获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					this.userInfo = res.data
					app.globalData.userInfo = res.data
					console.log(this.userInfo.mobile)
				}
			})
		},
		//获取数据
		getList() {
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/index`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.status_code == 200) {
						let pageNode = res.data.data
						this.pageNode = pageNode
					} else {
						uni.showToast({
							title: res.data.message
						});
					}

				}
			})
		},
	}
}
